<template>
<div>
    <div class="navbar-div">
        <van-nav-bar
            title=""
            left-text=""
            left-arrow
            @click-left="onClickLeft"
        />
    </div>
    <div class="topimage-div">
        <img :src="goodsInfo.IMAGE1" width="100%"/>
    </div>
    <div class="goods-name">{{goodsInfo.NAME}}</div>
    <div class="goods-price">{{goodsInfo.PRESENT_PRICE | moneyFilter}}</div>
    <div>
        <van-tabs swipeable sticky>
        <van-tab title="">
          <div class="detail" v-html="goodsInfo.DETAIL">

          </div>
        </van-tab>
        <van-tab title="">
            
        </van-tab>
        </van-tabs>

    </div>
    

    <van-goods-action>
  <van-goods-action-icon
    icon="chat-o"
    text=""
    dot
  />
  <van-goods-action-icon
    icon="cart-o"
    :info="goodsCount"
    text=""
    @click="switchToCart"
  />
  <van-goods-action-button
    text=""
    type="warning"
    @click="addGoodsToCart"
  />
  <van-goods-action-button
    type="primary"
    text=""
  />
</van-goods-action>
</div>
</template>

<script>
import { Toast, Card } from 'vant'
import {toMoney} from '@/filter/moneyFilter.js'
import {mapState} from 'vuex'
export default {
filters:{
    moneyFilter(money){
        return toMoney(money);
    }
},
  data() {
    return {
      goodsId: "",
      goodsInfo:{},   //
    };
  },
  computed: {
    ...mapState({
      goodsCount:(state)=>{   //
        return state.cartList.reduce((pre,item)=>pre+parseInt(item.count),0);
      }
    })
  },
  created() {
    this.goodsId= this.$route.query.goodsId ?this.$route.query.goodsId : this.$route.params.goodsId
    this.getInfo();
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    async getInfo() {
      let response = await this.$axios.post("/goods/getGoodsInfo", {
        goodsId: this.goodsId
      });
      let { code, message } = response.data;
      console.log(message);
      if(code == 200){
          this.goodsInfo = message;
      }else{
          Toast.fail('');
      }
    },
    addGoodsToCart(){  //
      let cartid = this.goodsId;   //id
        let cartList = this.$store.state.cartList;
        if(cartList.find((item)=>item.ID==cartid)){
            Toast.success('');
        }
        let {userid} = this.$store.state.userinfo;
        let count = 1;
        this.$store.dispatch('actions_cart_add',{
          userid,
          cartid,
          count
        })
    },
    switchToCart(){  //
        this.$router.push({
          name:"cart"
        })
    }
  }
};
</script>

<style scoped>
 .goods-name{
        background-color: #fff;
    } 
    .goods-price{
        background-color: #fff;
    }
    .detail {
        font-size:0px;
    }

</style>


