<template>
    <div>
        <van-nav-bar
            title=""
            left-text=""
            left-arrow
            @click-left="goBack"
         />
         <div class="register-panel">
            <van-field 
                v-model="username"
                label=""
                icon="clear"
                placeholder=""
                required
                @click-icon="username=''"
                :error-message="usernameErrorMsg"
            />

            <van-field 
                v-model="password"
                type="password"
                label="" 
                placeholder=""
                required
                :error-message="passwordErrorMsg"
               
            />
            <div class="register-button">
                <van-button type="primary" @click="registerAction" size="large" :loading="openLoading"></van-button>
            </div>
         </div>
    </div>
</template>

<script>
    import { Toast } from 'vant'
    export default {
        data() {
            return {
                username: '',
                password: '',
                openLoading:false, //Loading
                usernameErrorMsg:'',  //
                passwordErrorMsg:'',  //
            }
        },
       methods: {
           goBack() {
               this.$router.go(-1)
           },
            registerAction(){
                this.checkForm() && this.axiosRegisterUser()  //
            },
           async axiosRegisterUser(){
               this.openLoading=true;
                let response = await this.$axios.post('/user/register',{
                    username:this.username,
                    password:this.password
                })
                let {code,message} = response.data;
                if(code==200){
                    Toast.success('');
                    this.$router.push('/login');
                }else{
                    Toast.fail('');
                    this.openLoading = false; //
                }
           },
           //****
           checkForm(){
              let isOk = true
              if(this.username.length<5){
                  this.usernameErrorMsg = "5"
                  isOk=false
              }else{
                  this.usernameErrorMsg = ''
              } 
              if(this.password.length<6){
                  this.passwordErrorMsg = '6'
                  isOk = false
              }else{
                  this.passwordErrorMsg = ''
              }
              return isOk
           } 
       }, 
    }
</script>

<style scoped>
    .register-panel{
        width:96%;
        border-radius: 5px;
        margin:20px auto;
        padding-bottom:80px;
    }
    .register-button{
        padding-top:10px;
    }
</style>


