<template>
    <div>
        <van-nav-bar
            title=""
            left-text=""
            left-arrow
            @click-left="goBack"
         />
         <div class="register-panel">
            <van-field 
                v-model="username"
                label=""
                icon="clear"
                placeholder=""
                required
                @click-icon="username=''"
                :error-message="usernameErrorMsg"
            />

            <van-field 
                v-model="password"
                type="password"
                label="" 
                placeholder=""
                required
                :error-message="passwordErrorMsg"
               
            />
            <div class="register-button">
                <van-button type="primary" @click="loginAction" size="large" :loading="openLoading"></van-button>
                <van-button type="primary" @click="registerAction" size="large" class="register-button"></van-button>
            </div>
         </div>
    </div>
</template>
<script>
    import { Toast } from 'vant'
    export default {
        data() {
            return {
                username: '',
                password: '',
                openLoading:false, //Loading
                usernameErrorMsg:'',  //
                passwordErrorMsg:'',  //
            }
        },
       methods: {
           goBack() {
               this.$router.go(-1)
           },
            loginAction(){
                this.checkForm() && this.axiosLoginUser()  //
            },
           async axiosLoginUser(){
               this.openLoading=true;
                let response = await this.$axios.post('/user/login',{
                    username:this.username,
                    password:this.password
                })
                let {code,message,data={}} = response.data;
                // console.log(data);
                let {userid,username,token} = data;
                // console.log(token);
                if(code==200 && token){
                    localStorage.setItem('token',token);
                    localStorage.setItem('userinfo',JSON.stringify({
                        username:username,
                        userid:userid
                    }))
                    // console.log(this.$store);
                    this.$store.dispatch('actions_userinfo',{  //
                        username,
                        userid
                    })
                    this.$store.dispatch('actions_cart_init',userid);  //
                    this.$store.dispatch('actions_ordersList_init',userid); //
                    Toast.success('');
                    this.$router.push('/');
                }else{
                    Toast.fail(message);
                    this.openLoading = false; //
                }
           },
           //****
           checkForm(){
              let isOk = true
              if(this.username.length<5){
                  this.usernameErrorMsg = "5"
                  isOk=false
              }else{
                  this.usernameErrorMsg = ''
              } 
              if(this.password.length<6){
                  this.passwordErrorMsg = '6'
                  isOk = false
              }else{
                  this.passwordErrorMsg = ''
              }
              return isOk
           },
           registerAction(){
               this.$router.push({
                   name:"register"
               })
           } 
       },
       created() {
           if(this.$store.state.userinfo.username){  //localStorage
               Toast.success('');
               this.$router.push('/');
           }
       }, 
    }
</script>

<style scoped>
    .register-panel{
        width:96%;
        border-radius: 5px;
        margin:20px auto;
        padding-bottom:80px;
    }
    .register-button{
       margin-top:20px
    }
</style>


